import './styles.css';
import listOfCountry from './templates/listOfCountries.hbs';
import countryDescription from './templates/СountryDescription.hbs';
import debounce from 'lodash.debounce';
import { error } from '../node_modules/@pnotify/core/dist/PNotify.js';
import '../node_modules/@pnotify/core/dist/BrightTheme.css';
const input = document.getElementById('country-input');
let countryName = '';
const itemsList = document.querySelector('.items-list');
const getUrl = () => {
  fetch(`https://restcountries.eu/rest/v2/name/${countryName}`)
    .then(response => {
      return response.json();
    })
    .then(data => {
      renderMarkup(data);
    });
  const renderMarkup = contries => {
    console.log(contries.length);  
    if (contries.length <= 10 && contries.length > 1) {
      itemsList.innerHTML = '';
      const markupList = contries.map(result => listOfCountry(result)).join('');
      itemsList.insertAdjacentHTML('beforeend', markupList);
    } else if (contries.length === 1) {
      itemsList.innerHTML = '';
      const markup = contries
        .map(result => countryDescription(result))
        .join('');
      itemsList.insertAdjacentHTML('beforeend', markup);
    } else if (contries.length === undefined) {
      itemsList.innerHTML = '';
      error({
        text: 'There is no such country in the list. Modify the query!',
        width: '400px',
        sticker: false,
        closer: false,
        delay: 1500,
      });
    } else {
      itemsList.innerHTML = '';
      error({
        text: 'Too many matches found. Please enter more specific query!',
        width: '400px',
        sticker: false,
        closer: false,
        delay: 1500,
      });
    }
  };
};
const handlerInput = () => {
  countryName = input.value;
  getUrl();
};
input.addEventListener('input', debounce(handlerInput, 500));